import { Box, ButtonBase, SxProps, Typography } from '@mui/material'
import { useAppSelector } from 'app/hooks'
import { selectAccountInfo } from 'features/auth/states/account/account.slice'
import Image from 'mui-image'
import { ReactElement } from 'react'
import { useTranslation } from 'react-i18next'
import { useNavigate } from 'react-router-dom'
import alertDialogStore from './AlertDialog/alertDialogStore'

type IconMethodButtonProps = {
  icon: string | ReactElement,
  label: string,
  iconSize?: number,
  sx?: SxProps,
  labelStyle?: SxProps,
  labelSize?: number,
  labelColor?: string,
  onPress?: () => void
}

export default function IconMethodButton({ icon, label, iconSize = 32, sx, labelStyle, onPress, labelColor, labelSize = 12 }: IconMethodButtonProps) {
  const accountInfo = useAppSelector(selectAccountInfo)
  const { t } = useTranslation()
  const navigate = useNavigate()
  const handleOnClick = () => {
    if (checkKYC()) {
      if (checkLinked()) {
        onPress?.()
      }
    }
  }

  const showPopupRequireKYC = () => {
    alertDialogStore.alertContent = {
      title: t('kyc.title'),
      subtitle: t('kyc.subtitle'),
      firstButtonTitle: t('kyc.verify'),
      secondButtonTitle: t('kyc.later'),
      onClickFirstButton: () => navigate('/kyc-introduce'),
      reverseButton: true
    }
  }

  const checkKYC = (): boolean => {
    switch (accountInfo?.kyc.state) {
      case 'APPROVED': {
        //Đã xác thực
        return true
      }
      case 'REJECTED': {
        //Đã từ chối
        showPopupRequireKYC()
        return false
      }
      case 'CANCELED': {
        //Đã huỷ
        showPopupRequireKYC()
        return false
      }
      case 'PENDING': {
        //Chờ duyệt
        showPopupRequireKYC()
        return false
      }
      case 'BANNED': {
        //Bị ban do sai nhìu lần
        showPopupRequireKYC()
        return false
      }
      default: {
        showPopupRequireKYC()
        return false
      }
    }
  }
  const checkLinked = (): boolean | undefined => {
    if (!accountInfo?.linked) {
      alertDialogStore.alertContent = {
        title: 'Yêu cầu liên kết',
        subtitle: 'Bạn cần liên kết ít nhất 1 ngân hàng để sử dụng ví',
        firstButtonTitle: 'Liên kết',
        secondButtonTitle: t('kyc.later'),
        onClickFirstButton: () => navigate('/list-bank'),
        reverseButton: true
      }
    }
    return accountInfo?.linked
  }

  return (
    <ButtonBase sx={{ flex: 1, backgroundColor: 'inherit', minHeight: 80 }} onClick={handleOnClick}>
      <Box sx={{ paddingTop: 2, paddingBottom: 2, ...sx }} display='flex' flexDirection='column' justifyContent='center' alignItems='center'>
        {typeof (icon) === 'string' ? <Image src={icon} width={iconSize} height={iconSize} duration={0} />
          : <Box height={iconSize}>{icon}</Box>}
        <Typography mt={1.5} variant='subtitle2' sx={labelStyle} color={labelColor || 'text.subtitle'} fontSize={labelSize}>{label}</Typography>
      </Box>
    </ButtonBase>
  )
}
