import { IconButton, Typography } from '@mui/material'
import { useAppSelector } from 'app/hooks'
import useTheme from 'app/theme'
import iconEye from 'components/Images/iconEye'
import iconEyeOpen from 'components/Images/iconEyeOpen'
import { FlexBox } from 'components/styled'
import { selectBalance } from 'features/auth/states/account/account.slice'
import Image from 'mui-image'
import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'
import { currencyFromNumber } from 'utils/format-utils'

function BalanceContent() {
  const { t } = useTranslation()
  const balance = useAppSelector(selectBalance)
  const [themes] = useTheme()
  const [showBalance, setShowBalance] = useState(false)
  return (
    <FlexBox sx={{ width: '100%', backgroundColor: 'inherit', justifyContent: 'space-between', alignItems: 'center' }}>
      <FlexBox justifyContent='flex-start' alignItems='center'>
        <Image src='images/defaultAvatarContact.svg' duration={0} width={16} height='auto' fit='contain' />
        <FlexBox pl={0.5} />
        <Typography variant='h5' textAlign='center' color='text.caption'>{t('home.payMEWalletBalance')}</Typography>
      </FlexBox>
      <FlexBox flex={1} justifyContent='flex-end' alignItems='center'>
        <Typography variant='h4' fontWeight='600' sx={{ textAlign: 'right', flex: 1 }} color='text.primary'>
          {showBalance ? currencyFromNumber(balance) : '*****'}
        </Typography>
        <FlexBox pl={1} />
        <IconButton
          sx={{ width: 20, height: 20, padding: 0 }}
          onClick={() => setShowBalance(!showBalance)}
        >
          {showBalance ? iconEye(themes) : iconEyeOpen(themes)}
        </IconButton>
      </FlexBox>
    </FlexBox>
  )
}

export default BalanceContent