import { Box, Button, Typography } from '@mui/material'
import useTheme from 'app/theme'
import checkedIcon from 'components/Images/checkedIcon'
import Modal from 'components/Modal'
import { ModalHandle } from 'components/Modal/types'
import Image from 'mui-image'
import React, { forwardRef, useEffect, useImperativeHandle, useRef } from 'react'
import { useTranslation } from 'react-i18next'
import { useNavigate } from 'react-router-dom'
import kycModalStore from '../store/kycModalStore'

const RequireKYCModal = forwardRef<ModalHandle>((props, ref) => {
  const modalRef = useRef<ModalHandle>(null)
  const navigate = useNavigate()
  const { t } = useTranslation()

  useEffect(() => {
    const openModalSubscription = kycModalStore._openModal.subscribe(value => {
      if (value) {
        modalRef.current?.open?.()
      }
      if (value === false) {
        modalRef.current?.close?.()
      }
    })
    return () => {
      openModalSubscription.unsubscribe()
    }
  }, [])

  useImperativeHandle(
    ref,
    () => ({
      open: () => modalRef.current?.open?.()
    })
  )

  return (
    <Modal ref={modalRef}>
      <Box pt={3} />
      <Box alignSelf='center'>
        <Image
          src="images/sample.png"
          width={80}
          height={80}
          fit="contain"
          duration={0}
        />
      </Box>
      <Box pt={3.5} />
      <Typography textAlign='left' variant='h4'>{t('kyc.requireModalTitle')}</Typography>
      <Line title={t('kyc.requireModalSubtitle1')} />
      <Line title={t('kyc.requireModalSubtitle2')} />
      <Line title={t('kyc.requireModalSubtitle3')} />
      <Box pt={3} />
      <Button variant='contained' onClick={() => navigate('/kyc-introduce')} sx={{ height: 48, borderRadius: 2.5 }} >{t('kyc.verifyNow')}</Button>
    </Modal>
  )
})
type Props = { title: string }
const Line = ({ title }: Props) => {
  const [themes] = useTheme()
  return (
    <Box pt={1.5} display='flex' flexDirection='row' alignItems='center'>
      {checkedIcon(themes)}
      <Box pr={0.6} />
      <Typography variant='subtitle1'>{title}</Typography>
    </Box>
  )
}

export default RequireKYCModal
