import { Typography } from '@mui/material'
import { useAppSelector } from 'app/hooks'
import { FlexBox } from 'components/styled'
import { selectAccountInfo } from 'features/auth/states/account/account.slice'
import Image from 'mui-image'
import React from 'react'
import { useTranslation } from 'react-i18next'

function KYCState() {
  const { t } = useTranslation()
  const accountInfo = useAppSelector(selectAccountInfo)
  if (accountInfo?.kyc.state === 'PENDING') {
    return (
      <FlexBox
        alignItems='center'
        width='100%'
        p={2.5}
        borderRadius={2.5}
        mt={3}
        bgcolor='background.pending'
      >
        <Image src='images/kyc/kycPendingIcon.svg' width={40} height='auto' fit='cover' duration={0} />
        <FlexBox pl={0.5} />
        <FlexBox flexDirection='column'>
          <Typography variant='h5' color='text.pending'>{t('home.kycPendingTitle')}</Typography>
          <Typography variant='subtitle2' color='text.caption'>{t('home.kycPendingSubtitle')}</Typography>
        </FlexBox>
      </FlexBox>
    )
  }
  return null
}

export default KYCState