import { FlexBox } from 'components/styled'
import React from 'react'
import { useNavigate } from 'react-router-dom'
import BalanceContent from './BalanceContent'
import IconButton from '../../../components/IconMethodButton'
import depositIcon from 'components/Images/depositIcon'
import transferIcon from 'components/Images/transferIcon'
import scanIcon from 'components/Images/scanIcon'
import withdrawIcon from 'components/Images/withdrawIcon'
import { useTranslation } from 'react-i18next'
import useTheme from 'app/theme'
import alertDialogStore from 'components/AlertDialog/alertDialogStore'

function MainMethod() {
  const navigate = useNavigate()
  const { t } = useTranslation()
  const [themes] = useTheme()

  const handleOpenDeposit = () => {
    navigate('/deposit')
  }

  const handleOpenTransfer = () => {
    navigate('/transfer')
  }

  const handleOpenWithdraw = () => {
    navigate('/withdraw')
  }

  const handleOpenPay = () => {
    alertDialogStore.alertContent = {
      title: t('kyc.title'),
      subtitle: t('kyc.subtitle'),
      firstButtonTitle: t('kyc.verify'),
      secondButtonTitle: t('kyc.later'),
      onClickFirstButton: () => navigate('/kyc-introduce'),
      reverseButton: true
    }
    // alertDialogStore.alertContent = {
    //   title: 'Thông báo',
    //   subtitle: 'Tính năng đang phát triển',
    //   firstButtonTitle: 'Đã hiểu'
    // }
  }

  return (
    <FlexBox
      width='100%'
      flexDirection='column'
      bgcolor='background.default'
      borderRadius={2.5}
      overflow='hidden'
      boxShadow='0px 5px 17px 1px rgba(18, 18, 18, 0.05)'
    >
      <FlexBox
        m={2.5}
        pb={2.5}
        mb={0}
        sx={{
          borderBottom: '1px solid',
          borderColor: 'colors.light'
        }}
      >
        <BalanceContent />
      </FlexBox>
      <FlexBox width='100%'>
        <IconButton onPress={handleOpenDeposit} icon={depositIcon(themes)} label={t('home.deposit')} />
        <IconButton onPress={handleOpenPay} icon={scanIcon(themes)} label={t('home.pay')} />
        <IconButton onPress={handleOpenTransfer} icon={transferIcon(themes)} label={t('home.transfer')} />
        <IconButton onPress={handleOpenWithdraw} icon={withdrawIcon(themes)} label={t('home.withdraw')} />
      </FlexBox>
    </FlexBox>
  )
}

export default MainMethod