import { Box } from '@mui/system';
import { appSagaAction } from 'app/app.saga';
import { changeBiometric, selectBiometric, selectConfigs, selectDeviceInfo } from 'app/app.slice';
import { bridge } from 'app/communicateBridge';
import { useAppDispatch, useAppSelector } from 'app/hooks';
import alertDialogStore from 'components/AlertDialog/alertDialogStore';
import biometricStore from 'components/BiometricTypeButton/biometricStore';

import Meta from 'components/Meta';
import { FullSizeFlexBox, ScrollableFlexBox } from 'components/styled';
import { accountSagaAction } from 'features/auth/states/account/account.saga';
import { selectAccountInfo } from 'features/auth/states/account/account.slice';
import { selectAccessToken } from 'features/auth/states/auth/auth.slice';
import { bankSagaAction } from 'features/bank/states/bank.saga';
import { useEffect } from 'react';
import { usePaddingBottom, usePaddingTop } from 'utils';
// import { b64toBlob, dataUriToBlob } from 'utils/format-utils';
// import { useEffect, useRef } from 'react';
// import { history } from 'utils';
import Footer from '../../components/Footer';
import Header from './components/Header';
import HistoryList from './components/HistoryList';
import KYCState from './components/KYCState';
import MainMethod from './components/MainMethod';
import RecommendedService from './components/RecommendedService';
import RequireKYCModal from './components/RequireKYCModal';
import {historySagaAction} from '../history/states/history.saga';

function Home() {
  const dispatch = useAppDispatch()
  const biometric = useAppSelector(selectBiometric)
  const deviceInfo = useAppSelector(selectDeviceInfo)
  const accountInfo = useAppSelector(selectAccountInfo)
  const accessToken = useAppSelector(selectAccessToken)
  const paddingTop = usePaddingTop()
  const paddingBottom = usePaddingBottom()
  const configs = useAppSelector(selectConfigs)

  useEffect(() => {
    if (biometric.isShowRegisterBiometricPopup && !biometric.isEnabled) {
      checkBiometric()
    }
    dispatch(bankSagaAction.getBankList())
    dispatch(accountSagaAction.getBalance())
    dispatch(bankSagaAction.getLinkedList({ phone: accountInfo?.phone }, () => { }))
    dispatch(historySagaAction.getHistoryList({filter: {}, paging: {},  opts: {}}))
    if (configs.length === 0)
      dispatch(appSagaAction.getSettingConfigs())
  }, [])

  const checkBiometric = () => {
    if (deviceInfo.biometric?.isSupport && (deviceInfo.biometric?.type === 'FACE_ID' || deviceInfo.biometric?.type === 'TOUCH_ID' || deviceInfo.biometric?.type === 'UNKNOWN')) {
      alertDialogStore.alertContent = {
        title: 'Bạn có muốn bật sinh trắc học?',
        subtitle: 'Dùng sinh trắc học sẽ giúp bạn đăng nhập nhanh chóng không cần PayME PIN.',
        firstButtonTitle: 'Bật sinh trắc',
        isColumn: true,
        onClickFirstButton: () => {
          biometricStore.callback = {
            onSuccess: () => {
              dispatch(changeBiometric({
                isEnabled: true,
                isShowRegisterBiometricPopup: false,
                biometricKey: accessToken
              }))
            }
          }
          bridge.next({
            type: 'jsBiometricAuthentication',
            data: {
              description: 'Cần sinh trắc để tiếp tục',
              cancelTitle: 'Huỷ'
            }
          })
        },
        secondButtonTitle: 'Để sau',
        reverseButton: true
      }
    }
  }

  return (
    <>
      <Meta title="Home" />
      <FullSizeFlexBox
        sx={{
          paddingTop: `${paddingTop}px`,
          paddingBottom: `${paddingBottom}px`,
          justifyContent: 'flex-start',
          height: '100vh',
          overflow: 'hidden',
          background: 'linear-gradient(to bottom, #DCFFCF 0%, #FFFFFF 40%, #FFFFFF 100%);',
          scrollbar: 'none',
        }}
        display="flex"
        flexDirection="column"
      >
        <Box
          width='100%'
          pl={2.5}
          pr={2.5}
        >
          <Header />
          <Box sx={{ paddingTop: 2.5 }} />
          <MainMethod />
          <KYCState />
          <Box sx={{ paddingTop: 3 }} />
        </Box>
        <ScrollableFlexBox>
          <RecommendedService />
          <Box width='100%' pt={1} bgcolor='background.lightGray' />
          <HistoryList />
          <Box sx={{ paddingTop: 8 }} />
        </ScrollableFlexBox>
        <Footer />
      </FullSizeFlexBox>
      <RequireKYCModal />
    </>
  );
}

export default Home;
