import { Box, ButtonBase, Typography } from '@mui/material'
import useTheme from 'app/theme'
import financeIcon from 'components/Images/financeIcon'
import walletIcon from 'components/Images/walletIcon'
import Image from 'mui-image'
import { useTranslation } from 'react-i18next'
import { selectDeviceInfo } from 'app/app.slice'
import { useAppSelector } from 'app/hooks'
import { FlexBox } from './styled'
import { useNavigate } from 'react-router-dom'

export default function Footer() {
  return (
    <AppFooter />
  )
}

const MiniAppFooter = () => {
  const deviceInfo = useAppSelector(selectDeviceInfo)
  return (
    <FlexBox
      flexDirection='row'
      alignItems='center'
      justifyContent='center'
      sx={{
        position: 'fixed',
        bottom: 0,
        backgroundColor: 'background.default',
        width: '100vw',
        height: 36,
        pb: deviceInfo?.platform.toUpperCase() === 'IOS' || deviceInfo?.platform.toUpperCase() === 'ANDROID' ? 0 : deviceInfo.insets.bottom / 8,
      }}
    >
      <Typography color='#929292' fontSize={12} sx={{ whiteSpace: 'pre' }}>Power by  </Typography>
      <Image src='images/payMeLogoFooter.svg' height={14} width='auto' duration={0} />
    </FlexBox>
  )
}

const AppFooter = () => {
  const deviceInfo = useAppSelector(selectDeviceInfo)
  const navigate = useNavigate()
  const [themes] = useTheme()
  const { t } = useTranslation()
  return (
    <Box
      sx={{
        position: 'fixed',
        bottom: 0,
        left: 0,
        backgroundColor: 'background.default',
        width: '100vw',
        pb: deviceInfo?.platform.toUpperCase() === 'IOS' || deviceInfo?.platform.toUpperCase() === 'ANDROID' ? deviceInfo.insets.bottom / 8 : 0,
        boxShadow: '0 -1px rgba(18, 18, 18, 0.05)',
      }}
      display='flex'
      flexDirection='row'
    >
      <ButtonBase sx={{ padding: 1, display: 'flex', flex: 1 }}>
        <Box display='flex' flexDirection='column' justifyContent='center' alignItems='center'>
          {walletIcon(themes, true)}
          <Box mt={0.75} />
          <Typography variant='subtitle2' fontWeight='600' color='text.secondary'>{t('home.tabWalletTitle')}</Typography>
        </Box>
      </ButtonBase>
      <ButtonBase disabled sx={{ padding: 1, display: 'flex', flex: 1 }}>
        <Image src='images/Logo.svg' height={52} width={52} fit='fill' duration={0} />
      </ButtonBase>
      <ButtonBase sx={{ padding: 1, display: 'flex', flex: 1 }}
        onClick={() => navigate('/link-bank')}
      >
        <Box display='flex' flexDirection='column' justifyContent='center' alignItems='center'>
          {financeIcon(themes, false)}
          <Box mt={0.75} />
          <Typography variant='subtitle2' color='text.gray'>{t('home.tabFinanceTitle')}</Typography>
        </Box>
      </ButtonBase>
    </Box>
  )
}