export const getTextGreetings = () => {
  const h = new Date().getHours();
  if (h >= 0 && h < 11) return 'auth.goodMorning'
  if (h < 14) return 'auth.goodAfternoon1'
  if (h < 18) return 'auth.goodAfternoon2'
  return 'auth.goodEvening'
}

export const getGreetingName = (name: string) : string => {
  const text = name.split(' ')
  if (text.length <= 2) return name || ''

  return text.slice(-2).join(' ')
}
